___CSS_LOADER_EXPORT___.push([module.id, `:root {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    --clr-tmp-heat: linear-gradient(#FF5733, #FF8C00, #FFD700);
    --clr-tmp-med: linear-gradient(to bottom, #80E0A7, #40C4FF, #0095B6);
    --clr-tmp-cold: linear-gradient(to bottom, #3B4C76, #1F2A44, #5A2D9A);
    --fs-500: 1rem;
    --fs-700: 2.5rem;
    --fw-normal: 500;
    --fw-bold: 700;
}

.heat {
    background: var(--clr-tmp-heat);
}

.medium {
    background: var(--clr-tmp-med);
}

.cold {
    background: var(--clr-tmp-cold);
}

body {
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    gap: 3rem;
}

#weather-form {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    height: 10vh;
    background-color: #1F2A44;
    position: relative;
}

#weather-form::before {
    content: "Weather App";
    color: #fff;
    font-size: var(--fs-700);
    font-weight: var(--fw-bold);
}

.wrapper {
    display: flex;
    gap: 1rem;
}

#locationValue {
    padding: 0.2rem 0.4rem;
    border-radius: 0.2rem;    
}

#locationValue:focus {
    outline: none;
}

input[type=submit]{
    pad: 0.2rem 0.4rem;
    color: #fff;
    background-color: #0e172c;
    border-radius: 0.2rem;
    border: none;
}

.main-content {
    display: flex;
    flex-flow: column;
}

.city {
    font-weight: var(--fw-bold);
    text-align: center;
}

.weather-img {
    width: 300px;
    height: 300px;
}

.main-content {
    border-radius: 0.2rem;
    display: flex;
    gap: 1rem;
    flex-direction: column;
    align-items: center;
    padding: 1rem;
    width: 50vw;
    height: 70vh;
}

a{
    color: #1F2A44;
    text-decoration: none;
}

a:hover {
    color: #0348e7;
    text-decoration: underline;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,mJAAmJ;IACnJ,0DAA0D;IAC1D,oEAAoE;IACpE,qEAAqE;IACrE,cAAc;IACd,gBAAgB;IAChB,gBAAgB;IAChB,cAAc;AAClB;;AAEA;IACI,+BAA+B;AACnC;;AAEA;IACI,8BAA8B;AAClC;;AAEA;IACI,+BAA+B;AACnC;;AAEA;IACI,aAAa;IACb,iBAAiB;IACjB,uBAAuB;IACvB,mBAAmB;IACnB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,6BAA6B;IAC7B,WAAW;IACX,YAAY;IACZ,yBAAyB;IACzB,kBAAkB;AACtB;;AAEA;IACI,sBAAsB;IACtB,WAAW;IACX,wBAAwB;IACxB,2BAA2B;AAC/B;;AAEA;IACI,aAAa;IACb,SAAS;AACb;;AAEA;IACI,sBAAsB;IACtB,qBAAqB;AACzB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,kBAAkB;IAClB,WAAW;IACX,yBAAyB;IACzB,qBAAqB;IACrB,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,iBAAiB;AACrB;;AAEA;IACI,2BAA2B;IAC3B,kBAAkB;AACtB;;AAEA;IACI,YAAY;IACZ,aAAa;AACjB;;AAEA;IACI,qBAAqB;IACrB,aAAa;IACb,SAAS;IACT,sBAAsB;IACtB,mBAAmB;IACnB,aAAa;IACb,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,cAAc;IACd,qBAAqB;AACzB;;AAEA;IACI,cAAc;IACd,0BAA0B;AAC9B","sourcesContent":[":root {\n    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;\n    --clr-tmp-heat: linear-gradient(#FF5733, #FF8C00, #FFD700);\n    --clr-tmp-med: linear-gradient(to bottom, #80E0A7, #40C4FF, #0095B6);\n    --clr-tmp-cold: linear-gradient(to bottom, #3B4C76, #1F2A44, #5A2D9A);\n    --fs-500: 1rem;\n    --fs-700: 2.5rem;\n    --fw-normal: 500;\n    --fw-bold: 700;\n}\n\n.heat {\n    background: var(--clr-tmp-heat);\n}\n\n.medium {\n    background: var(--clr-tmp-med);\n}\n\n.cold {\n    background: var(--clr-tmp-cold);\n}\n\nbody {\n    display: flex;\n    flex-flow: column;\n    justify-content: center;\n    align-items: center;\n    gap: 3rem;\n}\n\n#weather-form {\n    display: flex;\n    align-items: center;\n    justify-content: space-around;\n    width: 100%;\n    height: 10vh;\n    background-color: #1F2A44;\n    position: relative;\n}\n\n#weather-form::before {\n    content: \"Weather App\";\n    color: #fff;\n    font-size: var(--fs-700);\